#layout {
  position: relative;
  margin-top: 60px;
  overflow: hidden;
  transition: height .5s ease;
  transform: translateZ(0);
  display: flex;
  flex-direction: column-reverse;
}

#layout .img {
  position: absolute;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: all .5s ease;
}

#layout .img.show {
  opacity: 1;
  visibility: visible;
}

#layout .img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}
